<template>
  <div>
    <Navbar/>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in gallery" :key="item.id">
        <img v-lazy="item.img_url" alt="" width="100%" style="display:block" />
      </van-swipe-item>
    </van-swipe>
    <Tips />
    <div class="info">
      <div class="title">{{ info.name }}</div>
      <div class="goods_brief">{{ info.goods_brief }}</div>
      <div class="retail_price">{{ info.retail_price | RMBformat }}</div>
    </div>
    <van-cell title="" is-link @click="isSkuShow=true" />
    
    <div class="attribute">
      <h3></h3>
      <ul>
        <li v-for="(item, key) in attribute" :key="key">
          <div class="l">{{ item.name }}</div>
          <div class="r van-ellipsis">{{ item.value }}</div>
        </li>
      </ul>
    </div>
    <div class="goods_desc" v-html="goods_desc"></div>
    <Products :goodsList="goodsList" />
    <!-- sku -->
     <!-- refsku -->
    <van-sku
      ref="sku"  
      v-model="isSkuShow"
      :sku="sku"
      :goods="goods"
      :hide-stock="sku.hide_stock"
    />
    <AppGoodActive @goToCart="goToCart" :goodsCount="goodsCount" />
  </div>
</template>

<script>
import { GetGoodsDetailData, GetGoodsRelatedData, GetGoodsCount,AddProductToCart } from "@/request/api";
import Tips from "@/components/Tips";
import Products from "@/components/Products";
import AppGoodActive from "@/components/AppGoodActive";
import Navbar from '@/components/Navbar.vue'
export default {
  data() {
    return {
      isSkuShow:false,
      sku:{
          tree:[],
          hide_stock:false,  // 
           price: '0', // 
           stock_num: 0, // 
      },
      goods:{
           picture:""
      },





      //
      gallery: [],
      //
      info: {},
      //
      attribute: [],
      //
      goods_desc: "",
      //
      issue: [],
      //
      goodsList: [],
      //
      goodsCount:0,
      productList:[]
    }
  },
  components: {
    Tips,
    AppGoodActive,
    Products,
    Navbar
  },
  created() {
    //
    GetGoodsDetailData({
      id: this.$route.query.id,
    }).then((res) => {
      // console.log(res);
      //  console.log(this.gallery);
      // let {gallery}=res.data.data;
      // this.gallery=gallery;
      this.gallery = res.data.data.gallery;
      this.info = res.data.data.info;
      this.attribute = res.data.data.attribute;
      this.goods_desc = this.info.goods_desc;
      //   console.log(this.goods_desc);
      this.issue = res.data.data.info;
      this.productList=res.data.data.productList
      // console.log(this.productList);




      //sku
      this.goods.picture = this.info.list_pic_url
      this.sku.price = this.info.retail_price
      this.sku.stock_num = this.info.goods_number
    });
    //
    GetGoodsRelatedData({
      id: this.$route.query.id,
    }).then((res) => {
      //  console.log(res.data.data);
      this.goodsList = res.data.data.goodsList;
      
    });
  // token
    let token = localStorage.getItem('token');
    if (token) {
      // 
      this.GetCount();
       //
  
    }

  //   //
  //  GetGoodsCount().then(res=>{
  //    if(res.data.errno==0){
  //     //  console.log(res);//goodsCount
  //     this.goodsCount=res.data.data.cartTotal.goodsCount
  //     // console.log(this.goodsCount);
  //    }
  //  })
  },
  methods:{
      //

   GetCount(){
    GetGoodsCount().then(res=>{
     if(res.data.errno==0){
      //  console.log(res);//goodsCount
      this.goodsCount=res.data.data.cartTotal.goodsCount
      // console.log(this.goodsCount);
     }
   })
    },
    goToCart(){
      //skusku
      //sku
      if(this.isSkuShow){
          //
          // console.log("");
          //
          // console.log(this.$refs.sku.getSkuData().selectedNum);
          //
           let token = localStorage.getItem('token')
           if(token){
          AddProductToCart({
            goodsId:this.$route.query.id,
            productId:this.productList[0].id,
            number:this.$refs.sku.getSkuData().selectedNum
          }).then(res=>{
            if(res.data.errno==0){
              // console.log(res);
              //
              //1
              this.goodsCount=res.data.data.cartTotal.goodsCount
              //2
              this.$toast.success("");
              setTimeout(()=>{
                // this.$router.push({name:'Cart'});
                 this.$router.push("/cart");
              },2000)

            }
          })
           }else{
              // this.$router.push("/user");
               this.$toast.loading("")
               setTimeout(()=>{
                // this.$router.push({name:'Cart'});
                 
                 this.$router.push("/user");
              },1500)

           }

      }else{
        this.isSkuShow=true
      }
    },
  },
  watch: {
    $route(to, from) {
      if (to.path == from.path) return;
      this.$router.go(0); // 
    },
  }

};
</script>

<style lang="less" scoped>
.info {
  background-color: #fff;
  text-align: center;
  line-height: 0.4rem;
  padding: 0.1rem 0;
  border-bottom: 1px solid #ccc;
  .title {
    font-size: 0.2rem;
  }
  .retail_price {
    color: darkred;
  }
}
.attribute {
  background-color: #fff;
  margin-top: 0.2rem;
  padding: 0.1rem 0.168rem;
  h3 {
    font-size: 0.16rem;
    line-height: 0.4rem;
  }
  li {
    height: 0.4rem;
    line-height: 0.4rem;
    background-color: #efefef;
    display: flex;
    margin-bottom: 0.1rem;
    border-radius: 0.05rem;

    .l {
      width: 30%;
      text-align: right;
      margin-right: 0.1rem;
    }
    .r {
      flex: 1;
    }
  }
}
//ui
/deep/.goods_desc {
  img {
    display: block;
    width: 100%;
  }
}
</style>
