<template>
  <div>
    <van-dropdown-menu>
      <van-dropdown-item title="" disabled />
      <van-dropdown-item @change="priceChange" title="" v-model="priceVal" :options="option2" />
      <van-dropdown-item @change="categoryChange" title="" v-model="categoryVal" :options="filterCategory" />
    </van-dropdown-menu>
    <van-empty v-if="goodsList.length==0" image="search" description="" />
    <Products v-else :goodsList="goodsList"/>
  </div>
</template>

<script>
import Products from "./Products"
export default {
  data() {
    return {
      priceVal:" ",//id
      categoryVal:"",
     //categoryVal:1005000,//value2,value
      option2: [
        { text: "", value: "desc" },
        { text: "", value: "asc" },
      ],
    };
  },
  props:["filterCategory","goodsList"],//filterCategoryoptions2[{text:"",value0}{text:"",value:100500}]
  components:{
    Products,
  },
  methods:{
    categoryChange(value){
      // console.log(value);//valueid
      this.$emit("categoryChange",value)
    },
    priceChange(value){
      //  console.log(value);//desc asc
       this.$emit("priceChange",value)
    }
  },
  // categoryVal:0,
  beforeUpdate(){
    //filterCateorycheckedtrue
    // checkedtruethis.categoryValvalue
    
    this.filterCategory.map(item=>{
     if(item.checked){
      // console.log("item",item);
      this.categoryVal=item.value
      return
    }
    })
  }
};
</script>

<style></style>
