<template>
  <div>
    <van-row @click="openModal">
      <van-col span="6">
        <img :src="avatarSrc" alt="" />
      </van-col>
      <van-col span="15">{{ nickname }}</van-col>
      <van-col span="3">
      <van-icon :name="Logined ? 'cross' : 'arrow'" @click="loginOut" />
      </van-col>
    </van-row>
    <van-grid :column-num="3" square>
      <van-grid-item icon="photo-o" text="" />
      <van-grid-item icon="photo-o" text="" />
      <van-grid-item icon="photo-o" text="" />
      <van-grid-item icon="photo-o" text="" />
    </van-grid>
    <!--  -->
    <transition name="van-fade">
      <div class="login-modal" v-show="isShowModal">
        <div class="close-modal" @click="isShowModal = !isShowModal"></div>
        <van-form @submit="onSubmit" class="login-form">
          <van-field
            v-model="username"
            name=""
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
          />
          <van-field
            v-model="password"
            type="password"
            name=""
            label=""
            placeholder=""
            :rules="[{ required: true, message: '' }]"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit"
              ></van-button
            >
          </div>
        </van-form>
      </div>
    </transition>
  </div>
</template>

<script>
import { GoLogin } from "@/request/api";
export default {
  name: "User",
  data() {
    return {
      avatarSrc: require("../assets/avatar.png"),
      username: "",
      password: "",
      isShowModal: false,
      nickname: "",
      // 
      Logined: false,
    };
  },
  created(){
    //localStoragetoken
    //token
    let token = localStorage.getItem("token");
    if(token){
      //
      let userInfo = JSON.parse(localStorage.getItem("userInfo"));
      this.nickname = userInfo.nickname;
      this.avatarSrc = userInfo.avatar;
       this.Logined=true;
    }
  },
  methods: {
    onSubmit(values) {
      // console.log("submit", values);
      // console.log('username',values[""]);
      // console.log('pwd',values['']);
      let username = values[""];
      let pwd = values[""];
      //  GoLogin({username:values[""],pwd:values[""]})
      GoLogin({ username, pwd }).then((res) => {
        if (res.data.errno == 0) {
          // console.log(res.data.data);

          //
          // 
          // 1
          this.$toast.success("");
          // 2token
          localStorage.setItem("token", res.data.data.token);
          localStorage.setItem(
            "userInfo",
            JSON.stringify(res.data.data.userInfo)
          );
           this.Logined=true;
          // 31s
          setTimeout(() => {
            this.isShowModal = !this.isShowModal;
            // 4userinfo
            this.nickname = res.data.data.userInfo.nickname;
            // console.log(this.nickname);
            this.avatarSrc = res.data.data.userInfo.avatar;
          }, 1000);
        //  console.log(this.Logined);

           if (this.$store.state.ifgoBack==false) {
            // 
            this.$toast.loading("")
            // 
            setTimeout(()=>{
              this.$router.back();
              // false
              this.$store.commit("changeIfgoBack", false);
            }, 2000)
            console.log(this.$store.state.ifgoBack);
           }
           
        // else {
        //   this.$toast.fail(res.errmsg)
        // }
        }
      });
    },
    //
    loginOut(){
      // console.log(222);
       if (this.Logined) {
        this.$dialog
          .confirm({
            title: "",
            message: "",
          }) .then(() => {
            // token
            localStorage.removeItem('token')
            localStorage.removeItem('userInfo')
            // 
            
            this.$router.go(0)
          })
          .catch(err => err)
      } else {
        // van-row
        // 
        // this.showModal = true
      }
    },
    openModal(){
      //returnisShowModal
      let token=localStorage.getItem("token");
      if(token){
        return
      }
      this.isShowModal=!this.isShowModal
    }
  },
};
</script>

<style lang="less" scoped>
.van-row {
  padding: 0.2rem 4%;
  background-color: #333;
  color: #fff;
  .van-col {
    line-height: 0.7rem;
    font-size: 0.15rem;
    img {
      width: 0.7rem;
      border-radius: 50%;
      display: block;
    }
    &:last-child {
      text-align: right;
    }
  }
}
.login-modal {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  // background-color: rgba(0, 0, 0, 0.5);
  z-index: 9;
  .login-form {
    width: 90%;
    background-color: #fff;
    padding-bottom: 0.2rem;
    position: absolute;
    left: 50%;
    top: 35%;
    margin-left: -45%;
  }
  .close-modal {
    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, 0.5);
  }
}
</style>
